html {
  font-family: 'Raleway', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  color: #333;
  background: rgb(246, 245, 245);
  font-size: 10px;
  height: 100%;
  overflow-x: hidden;
}

.content {
  min-height: calc(100vh - 2rem);
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none;   /* Chrome/Safari/Opera */
  -moz-user-select: none;      /* Firefox */
  -ms-user-select: none;       /* Internet Explorer/Edge */
  user-select: none;
}

header {
  background: linear-gradient(#838383, rgb(246, 245, 245));
  display: flex;
  justify-content: space-between;
  align-items: center;
}

h1 {
  padding: 2rem;
  margin: 0;
  font-size: 3rem;
}

header div {
  padding-right: 1rem;
  display: grid;
  justify-items: end;
  gap: 3rem;
  font-size: 1rem;
}

.date {
  border-bottom: #333333 solid 1px;
}

.language{
  width: 1.2rem;
  font-size: 1.2rem;
  cursor: pointer;
  border-bottom: solid 1px rgba(253, 244, 229, 0);
}

.language:hover, .language:focus {
  color: seagreen;
  border-bottom: solid 1px seagreen ;
}

.currencies-block {
  display: flex;
  align-items: stretch;
  flex-wrap: wrap;
  margin-bottom: 2rem;
  background-color: #333;
  color: rgb(246, 245, 245);
  padding: 0 3rem 0 1.3rem;
}

.currencies-block div {
  width: 3rem;
  text-align: center;
  font-size: 1rem;
  cursor: pointer;
  padding: 0.7rem;
}

.currencies-block div:hover, .currencies-block div:focus{
  background-color: seagreen;
}


.tooltip {
  position: relative;
  display: inline-block;
}

.tooltiptext {
  font-size: 0.8rem;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 8rem;
  max-height: 3rem;
  bottom: 100%;
  left: 0;
  background-color: seagreen;
  color: rgb(246, 245, 245);
  text-align: center;
  padding: 0.2rem;

  position: absolute;
  opacity: 0;
  transition: opacity 0.5s;
}

.tooltip:hover .tooltiptext, .tooltip:focus .tooltiptext {
  visibility: visible;
  opacity: 1;
}

.inputs {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  gap: 1rem;
  padding: 0 2rem 2rem 2rem;
}
.input-item label span{
  font-size: 0.7rem;
}

.input-item div{
  display: flex;
  justify-content: space-between;
}

label {
  font-size: 1rem;
  color: #838383;
}

.delete {
  cursor: pointer;
  font-size: 1rem;
  opacity: 0;
  transition: opacity 1s;
}

.input-item:hover .delete, .delete:focus {
  visibility: visible;
  opacity: 1;
}

.delete:hover, .delete:focus {
  color: red;
}

input {
  display: block;
  margin-bottom: 1rem;
  max-width: 12rem;
  height: 2rem;
  font-size: 1.5rem;
  outline:none;
  background: transparent;
  border: none;
  font-family: 'Raleway', sans-serif;

}

input, input:before, input:after {
  -webkit-user-select: initial;
  -khtml-user-select: initial;
  -moz-user-select: initial;
  -ms-user-select: initial;
  user-select: initial;
}

footer {
  padding: 0 2rem;
}

footer a {
  text-decoration: none;
}

footer a:hover, footer a:focus{
  color: seagreen;
}

footer a:visited{
  color: #838383;
}